body {
  font-size: 1.2rem;
  font-family: "Roboto", sans-serif; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

.contenedor2 {
  max-width: 120rem;
  width: 80%;
  margin: 0 auto; }

h1, h2, h3, h4 {
  margin: 0;
  font-family: "PT Serif Caption", serif; }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.8rem; }

h3 {
  font-size: 2rem; }

h4 {
  font-size: 1.8rem; }

a {
  text-decoration: none;
  color: #70A1D7; }

a:hover {
  color: #A1DE93; }

img {
  width: 100%; }

/*---------- CREAR LOS MIXINS ----------*/
.bg-gris {
  background-color: #EEEEEE; }

.bg-verde {
  background-color: #A1DE93; }

.bg-rojo {
  background-color: #F47C7C; }

.bg-azul {
  background-color: #70A1D7; }

.bg-amarillo {
  background-color: #F7F48B; }

.bg-blanco {
  background-color: #fff; }

.centrar-texto {
  text-align: center; }

.centro-pantalla {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.campo-login {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0; }
  .campo-login input {
    padding: .2rem;
    border-radius: .5rem;
    border: none;
    background-color: #EEEEEE;
    margin-left: .4rem; }

.campo {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 1rem 0; }
  .campo input, .campo select {
    border: none;
    background-color: #EEEEEE;
    border-radius: .5rem; }

.btn-principal, .btn-secundario {
  background-color: #A1DE93;
  border: solid 3px #fff;
  border-radius: .7rem;
  padding: .5rem 1rem;
  cursor: pointer;
  font-weight: 700;
  color: #fff;
  margin-bottom: .8rem; }

.btn-principal:hover {
  background-color: transparent;
  border: solid 3px #A1DE93;
  color: #A1DE93; }

.btn-secundario {
  background-color: #70A1D7; }

.btn-secundario:hover {
  background-color: transparent;
  border: solid 3px #70A1D7;
  color: #70A1D7; }

.colum-2 {
  grid-column: span 2;
  width: 100%; }

.titulo {
  padding: 1rem; }

.login {
  background-color: #fff;
  border-radius: .8rem;
  width: 18rem;
  -webkit-box-shadow: 3px 4px 8px 0px rgba(50, 50, 50, 0.7);
  -moz-box-shadow: 3px 4px 8px 0px rgba(50, 50, 50, 0.7);
  box-shadow: 3px 4px 8px 0px rgba(50, 50, 50, 0.7); }

.login_contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.login_logo {
  width: 16rem;
  padding: 1rem 0; }

.login_form {
  padding-bottom: 1.66667rem;
  text-align: center; }

.login_form_contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.header {
  background-color: #fff; }

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 768px) {
    .contenido-header {
      flex-direction: row;
      justify-content: space-between;
      align-items: center; } }

.logo-header img {
  width: 16rem;
  padding: .5rem 0; }

.nav-principal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 1rem; }
  @media (min-width: 375px) {
    .nav-principal {
      flex-direction: row; } }
  @media (min-width: 768px) {
    .nav-principal {
      flex-direction: row;
      padding-bottom: 0; } }
  .nav-principal a {
    margin-left: 1rem; }

.hero-acapulco {
  background-image: url(../img/acapulco.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
  height: 25rem; }

.overlay {
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center; }

.hero-acapulco-contenido {
  color: #fff; }
  .hero-acapulco-contenido h1 {
    font-size: 2rem; }
  @media (min-width: 768px) {
    .hero-acapulco-contenido h1 {
      font-size: 3rem; } }

/*============= SECCIONES =============*/
.secciones-contenido {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 1rem; }
  @media (min-width: 1200px) {
    .secciones-contenido {
      display: flex;
      flex-direction: column; } }

.seccion {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 10rem;
  transition: .5s; }
  @media (min-width: 1200px) {
    .seccion {
      width: 10rem;
      height: 10rem; } }

.seccion:hover {
  transform: scale(1.1);
  -webkit-box-shadow: 4px 4px 10px 0px rgba(50, 50, 50, 0.67);
  -moz-box-shadow: 4px 4px 10px 0px rgba(50, 50, 50, 0.67);
  box-shadow: 4px 4px 10px 0px rgba(50, 50, 50, 0.67); }

.col-2 {
  grid-column: span 2; }

/*=================== secciones & about us ===================*/
.secciones-sobreNosotros {
  padding: 5rem 0; }
  @media (min-width: 1200px) {
    .secciones-sobreNosotros {
      display: flex;
      gap: 5rem; } }

.hero-taxco {
  background-image: url(../img/taxco.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
  height: 15rem;
  display: none; }
  @media (min-width: 1200px) {
    .hero-taxco {
      display: inherit; } }

@media (min-width: 1200px) {
  .form-altaEmpleado-contenido form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem; } }

.empleados-datos {
  padding-top: 1.66667rem; }
  @media (min-width: 1200px) {
    .empleados-datos {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 1rem; } }

@media (min-width: 1200px) {
  .form-altaDocEmpleado form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem; } }

@media (min-width: 1200px) {
  .form-ubicacionEmpleado form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem; } }

.form-ubicacionEmpleado {
  padding-bottom: 1.66667rem; }

.baja-empleado {
  padding-bottom: 15rem; }

.comprobantes {
  padding-bottom: 5rem; }

@media (min-width: 1200px) {
  .comprobantes-contenido form {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem; }
    .comprobantes-contenido form .btn-secundario, .comprobantes-contenido form .btn-principal {
      grid-column: 2 / 3; } }

.cambios-eliminacion {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-bottom: 9rem;
  width: 25rem; }

.comprobantes-tabla-contenido {
  padding-bottom: 2.5rem; }

.titulos-column {
  background-color: #F7F48B; }

.tabla-MostrarDatos {
  background-color: #fff;
  border: solid 0.3rem #F7F48B;
  text-align: center;
  width: 100%;
  border-radius: .5rem;
  -webkit-box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.75); }
  .tabla-MostrarDatos td {
    border-bottom: solid 0.2rem #F7F48B;
    padding: .5rem 0; }
  .tabla-MostrarDatos tr:last-of-type td {
    border-bottom: none; }

.footer {
  background-color: #fff;
  padding: 1rem 0; }
